<template>
  <div>
    <NavTemplent title=""></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold"></h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            range-separator=""
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
          <el-form-item label="">
            <el-select v-model="form.departmentId" placeholder="">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.site" placeholder="">
              <el-option v-for="item in siteList" :key="item.website_id" :value="item.website_id" :label="item.website_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getUserList">
              <i class="el-icon-search"></i>
              </el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row style="border: 1px solid #e6e3e3">
        <h5 style="font-weight: bolder;padding: 0 15px"></h5>
        <el-col :span="12" style="padding: 0 15px">
          <h5 style="text-align: center;font-weight: bolder">lastchic</h5>
          <div id="myChart3" style="width: 100%;height: 300px"></div>
        </el-col>
        <el-col :span="12" style="padding: 0 15px">
          <h5 style="text-align: center;font-weight: bolder">
            ROI 
          </h5>

          <el-tabs v-model="activeName">
            <el-tab-pane label="" name="personal">
              <el-table
                :data="promotionList"
                border
                style="width: 100%;height: 250px">
                <el-table-column
                  type="index">
                </el-table-column>
                <el-table-column
                  label="">
                  <template slot-scope="scope">
                    <span>{{scope.row.name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="">
                  <template slot-scope="scope">
                    <span>{{scope.row.department_name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="">
                  <template slot-scope="scope">
                    <span>{{scope.row.role_name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="ROI">
                  <template slot-scope="scope">
                    <span>{{scope.row.status}}</span>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
            <el-tab-pane label="" name="department">
              <el-table
                :data="promotionList"
                border
                style="width: 100%;height: 250px">
                <el-table-column
                  type="index">
                </el-table-column>
                <el-table-column
                  label="">
                  <template slot-scope="scope">
                    <span>{{scope.row.name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="">
                  <template slot-scope="scope">
                    <span>{{scope.row.department_name || '--'}}</span>
                  </template>
                </el-table-column>
                <el-table-column
                  label="">
                  <template slot-scope="scope">
                    <span>{{scope.row.role_name || '--'}}</span>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
          </el-tabs>
        </el-col>
      </el-row>
      <el-row class="tableTitle">
        <el-col :span="24">
          <h5></h5>
        </el-col>
      </el-row>
      <div>
        <div id="myChart" style="width: 100%;height: 300px"></div>
      </div>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="">
            <el-input v-model="form.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.mobile" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="password" v-model="form.password" placeholder="******"></el-input>
          </el-form-item>
          <el-form-item label="ROI">
            <el-input v-model="form.email" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.department_id" placeholder="">
              <el-option v-for="item in departmentList" :key="item.department_id" :value="item.department_id" :label="item.department_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.role_id" placeholder="" multiple>
              <el-option v-for="item in roleList" :key="item.role_id" :label="item.role_name" :value="item.role_id"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle"> </el-button>
          <el-button size="mini" type="primary" @click="save"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                siteList: null,
                editUserMessage: null,
                departmentList: [],
                activeName: 'personal',
                page: {
                    pageSize: 1,
                    pageNum: 10
                },
                editUse: null,
                dialogVisible: false,
                promotionList: [],
                roleList: [],
                total: 0,
                updateStatusData: null,
                menu: false,
                title: '',
                form: {
                    date: [],
                    departmentId: '',
                    password: '',
                    mobile: '',
                    userName: '',
                    department_id: '',
                    email: '',
                    name: '',
                    role_id: [],
                    status: 0
                }
            }
        },
        methods: {
            getSiteList() {
                this.$post('/getWebsiteList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    website_name: this.form.siteName
                }).then(res => {
                    this.siteList = res.list
                })
            },
            drawLine() {
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                myChart.setOption({
                    tooltip : {
                        trigger: 'axis',
                            axisPointer: {
                            type: 'cross',
                                label: {
                                backgroundColor: '#6a7985'
                            }
                        }
                    },
                    legend: {
                        data:['','','','','']
                    },
                    toolbox: {
                        feature: {
                            saveAsImage: {}
                        }
                    },
                    grid: {
                        left: '3%',
                            right: '4%',
                            bottom: '3%',
                            containLabel: true
                    },
                    xAxis : [
                        {
                            type : 'category',
                            boundaryGap : false,
                            data : ['','','','','','','']
                        }
                    ],
                        yAxis : [
                    {
                        type : 'value'
                    }
                ],
                    series : [
                    {
                        name:'',
                        type:'line',
                        stack: '',
                        areaStyle: {},
                        data:[120, 132, 101, 134, 90, 230, 210]
                    },
                    {
                        name:'',
                        type:'line',
                        stack: '',
                        areaStyle: {},
                        data:[220, 182, 191, 234, 290, 330, 310]
                    },
                    {
                        name:'',
                        type:'line',
                        stack: '',
                        areaStyle: {},
                        data:[150, 232, 201, 154, 190, 330, 410]
                    },
                    {
                        name:'',
                        type:'line',
                        stack: '',
                        areaStyle: {normal: {}},
                        data:[320, 332, 301, 334, 390, 330, 320]
                    },
                    {
                        name:'',
                        type:'line',
                        stack: '',
                        label: {
                            normal: {
                                show: true,
                                position: 'top'
                            }
                        },
                        areaStyle: {normal: {}},
                        data:[820, 932, 901, 934, 1290, 1330, 1320]
                    }
                ]
                });
            },
            drawLine3() {
                // domecharts
                let myChart = this.$echarts.init(document.getElementById('myChart3'));
                // 
                myChart.setOption({
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b}: {c} ({d}%)"
                    },
                    legend: {
                        bottom: 10,
                        orient: 'vertical',
                        x: 'right',
                        data:['','','','']
                    },
                    series: [
                        {
                            name:'',
                            type:'pie',
                            radius: ['50%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                        fontSize: '30',
                                        fontWeight: 'bold'
                                    }
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: false
                                }
                            },
                            data:[
                                {value:1548, name:''},
                                {value:335, name:''},
                                {value:310, name:''},
                                {value:234, name:''}
                            ]
                        }
                    ]
                });
            },
            getUserList() {
                this.$post('/getUserList', {
                    user_name: this.form.userName,
                    department_id: this.form.departmentId
                }).then(res => {
                    this.total = res.count;
                    this.userList = res.list
                })
            },
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list
                }).catch(err => {
                    this.$message.error(err)
                });
            },
            getRoleList() {
                this.$post('getRoleList').then(res => {
                    this.roleList = res.list.filter(item => {
                        return item.status === 1
                    })
                })
            },
            editUseMessage(data) {
                this.editUserMessage = data;
                this.dialogVisible = true;
                if (data.id) {
                    this.form = data;
                    this.menu = true;
                }else {
                    this.form.mobile = '';
                    this.form.email = '';
                    this.form.name = '';
                    this.form.department_id = '';
                    this.form.role_id = [];
                    this.menu = false;
                }
            },
            updateStatus(data) {
                this.editUserMessage = data;
                if (data.status === 1) {
                    this.dialogVisible = true;
                } else {
                    this.$post('editUserStatus', {
                        user_id: data.id
                    }).then(res => {
                        this.getUserList();
                        this.$message.success('')
                    })
                }
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.editUserMessage.id) {
                    if (this.form.mobile === this.editUserMessage.mobile) {
                        this.mobile = null
                    } else {
                        this.mobile = this.form.mobile
                    }
                    if (this.uditUser) {
                        this.$post('editUser', {
                            email: this.editUserMessage.email,
                            user_id: this.editUserMessage.id,
                            user_name: this.editUserMessage.name,
                            mobile: null,
                            department_id: this.form.department_id,
                            password: this.editUserMessage.password,
                            role_list: JSON.stringify(this.form.role_id)
                        }).then(res => {
                            this.$post('editUserStatus', {
                                user_id: this.editUserMessage.id
                            }).then(res => {
                                this.$message.success('');
                                this.dialogVisible = false;
                                this.$emit('close');
                                this.getUserList()
                            });
                        })
                    } else {
                        this.$post('editUser', {
                            email: this.form.email,
                            user_id: this.editUserMessage.id,
                            user_name: this.form.name,
                            mobile: this.mobile,
                            department_id: this.form.department_id,
                            password: this.form.password,
                        }).then(res => {
                            this.$message.success('');
                            this.dialogVisible = false;
                            this.$emit('close');
                            this.getUserList()
                        })
                    }

                }else {
                    console.log(this.form);
                    this.$post('signUp', this.form).then(res => {
                        this.$message.success('');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getUserList()
                    })
                }
            }
        },
        mounted() {
            this.getSiteList();
            this.getUserList();
            this.getDepartmentList();
            this.getRoleList();
            this.drawLine3();
            this.drawLine()
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    padding: 15px;
    h5{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
  .tab {
    margin: 10px;
    .active{
      background: #409EFF;
      color: #fff;
    }
    span {
      font-size: 14px;
      border-radius: 5px;
      border: 1px solid #e6e3e3;
      padding: 5px 10px;
    }
  }
</style>



